import React, { useState } from "react";
import NewExpense from "./components/NewExp/NewExpense";
import Expenses from "./components/ExpenseComps/Expenses";

const dummy_expenses = [
  {
    id: "e1",
    title: "Car Insurance",
    amount: 20000,
    date: new Date(2021, 6, 23),
  },
  {
    id: "e2",
    title: "Shopping",
    amount: 10000,
    date: new Date(2021, 8, 23),
  },
  {
    id: "e3",
    title: "New Phone",
    amount: 20000,
    date: new Date(2021, 9, 14),
  },
  {
    id: "e4",
    title: "Books",
    amount: 5000,
    date: new Date(2021, 8, 31),
  },
];

const App = () => {
  const [expenses, setExpenses] = useState(dummy_expenses);

  const addExpenseHandler = async (expense) => {
    await fetch(
      "https://expense-tracker-e360b-default-rtdb.firebaseio.com/expenses.json",
      {
        method: "POST",
        body: JSON.stringify(expense),
        headers: {
          "Content-Type": "application/json",
        },
      }
    );
    setExpenses((prevExpenses) => {
      return [expense, ...prevExpenses];
    });
  };
  return (
    <div>
      <NewExpense onAddExpense={addExpenseHandler} />
      <Expenses items={expenses} />
    </div>
  );
};

export default App;
